import React, {Component} from 'react';
import './css/main.css';
import MainPage from './components/MainPage';
import Contact from './components/Contact';
import Competences from './components/Competences';
import Experiences from './components/Experiences';
import Header from './components/Header';
import Footer from './components/Footer';



class App extends Component {
  constructor(props){
    super(props);
    this.TABS = {
      "main":0,
      "exp":1,
      "cpm":2,
      "con":3,
    
    }
    this.state = {
        tab: this.TABS.main,
        lang: 'fr',
    };
    }
    switchTab(tab){
        this.setState({tab:tab});
    }
    switchLang(){
        if(this.state.lang === "fr") {
        this.setState({lang:"en"});
        } else {
        this.setState({lang:"fr"});
        }

    }
    render(){
        return(
        <div className="container-fluid container-lg p-0 text-justify">
            <Header switchTab={(tab)=>this.switchTab(tab)} activeTab={this.state.tab} lang={this.state.lang} switchLang={()=>this.switchLang()} TABS={this.TABS}/>
        
            {this.state.tab === this.TABS.main ?
            <MainPage switchTab={(tab)=>this.switchTab(tab)} TABS={this.TABS} lang={this.state.lang}/> : ''
            }
            {this.state.tab === this.TABS.exp ? 
            <Experiences lang={this.state.lang}/> : ""
            }
            {this.state.tab === this.TABS.cpm ? 
            <Competences lang={this.state.lang}/> : ""
            }
            {this.state.tab === this.TABS.con ?
            <Contact lang={this.state.lang}/> : ""
            }
        
        
            <Footer lang={this.state.lang} />
        </div>
        )
    }
}

export default App;
